<template>
  <div>
    <v-row>
      <v-spacer />
      <v-text-field
        v-model="searchInfo"
        label="음식 검색"
        @keyup.enter="foodSearch"
      />
      <v-btn icon @click="foodSearch">
        <v-icon>mdi-magnify</v-icon>
      </v-btn>
      <v-spacer />
    </v-row>
    <v-expansion-panels>
      <v-expansion-panel
        v-for="searchResult in searchResults"
        :key="searchResult.food_no"
        style="box-shadow:none;"
      >
        <v-expansion-panel-header style="font-size:20px;" @click="openFood(searchResult.nutrient)">{{ searchResult.food_name }}</v-expansion-panel-header>
        <v-expansion-panel-content>
          <v-col style="padding:0px;">
            칼로리 : {{ searchResult.cal }} Kcal
          </v-col>
          <v-row style="padding:0px 10px 20px;">
            <span v-for="foodNutrient in searchResult.nutrient" :key="foodNutrient.nutrient" style="margin:0px 5px;">
              <span v-if="foodNutrient.nutrient == 'carb'">
                탄수화물 : {{ foodNutrient.content }} g
              </span>
              <span v-else-if="foodNutrient.nutrient == 'pro'">
                단백질 : {{ foodNutrient.content }} g
              </span>
              <span v-else-if="foodNutrient.nutrient == 'fat'">
                지방 : {{ foodNutrient.content }} g
              </span>
              <span v-else-if="foodNutrient.nutrient == 'na'">
                나트륨 : {{ foodNutrient.content }} mg
              </span>
              <span v-else-if="foodNutrient.nutrient == 'chol'">
                콜레스테롤 : {{ foodNutrient.content }} mg
              </span>
              <span v-else-if="foodNutrient.nutrient == 'sug'">
                당분 : {{ foodNutrient.content }} g
              </span>
            </span>
          </v-row>
          <div class="percent-bar">
            <div class="carb percent-bar--part">0%</div>
            <div class="prot percent-bar--part">0%</div>
            <div class="fat percent-bar--part">0%</div>
          </div>
          <v-row style="margin:10px 20px 0px 0px">
            <span style="font-weight:bolder; font-size:50px; line-height:10px; margin:8px 10px 0px; color:#BF4C3D;">·</span>
            <span>탄수화물</span>
            <span style="font-weight:bolder; font-size:50px; line-height:10px; margin:8px 10px 0px; color:green;">·</span>
            <span>단백질</span>
            <span style="font-weight:bolder; font-size:50px; line-height:10px; margin:8px 10px 0px; color:#1A7ABA;">·</span>
            <span>지방</span>
          </v-row>
          <v-btn class="col-12" style="box-shadow:none; color:black; font-weight:bold; margin-top:30px;" @click="dietPick(searchResult)">
            선택  
            <v-icon small>  mdi-cursor-pointer</v-icon>
          </v-btn>
        </v-expansion-panel-content>
      </v-expansion-panel>
    </v-expansion-panels>
    <v-row>
      <v-btn
        text
        :disabled="!searchValid" 
        style="width:90%; margin:0px auto;" 
        @click="showMore()"
      >
        상세 품목 더보기
        <v-icon>mdi-chevron-down</v-icon>
      </v-btn>
    </v-row>
    <br>
    <v-simple-table v-show="more">
      <template v-slot:default>
        <tbody>
          <v-expansion-panels>
            <v-expansion-panel
              v-for="item in subFoodList"
              :key="item.food_no"
            >
              <v-expansion-panel-header style="font-weight:bold; font-size:16px;">{{ item.food_name }}</v-expansion-panel-header>
              <v-expansion-panel-content>
                <v-col class="nutrient" style="font-size:15px;">
                  칼로리 : {{ item.cal }} Kcal
                </v-col>
                <v-row style="margin:0px auto;">
                  <div v-for="n in item.nutrient" :key="n.nutrient" class="nutrient ml-4 mr-3">
                    <span style="font-size:15px;">
                      <span v-if="n.nutrient == 'carb'">
                        탄수화물 : {{ n.content }} g
                      </span>
                      <span v-else-if="n.nutrient == 'pro'">
                        단백질 : {{ n.content }} g
                      </span>
                      <span v-else-if="n.nutrient == 'fat'">
                        지방 : {{ n.content }} g
                      </span>
                      <span v-else-if="n.nutrient == 'na'">
                        나트륨 : {{ n.content }} mg
                      </span>
                      <span v-else-if="n.nutrient == 'chol'">
                        콜레스테롤 : {{ n.content }} mg
                      </span>
                      <span v-else-if="n.nutrient == 'sug'">
                        당분 : {{ n.content }} g
                      </span>
                    </span>
                  </div>
                </v-row>
                <v-btn class="col-12" style="box-shadow:none; color:black; font-weight:bold; margin-top:30px;" @click="dietPick(item)">
                  선택  
                  <v-icon small>  mdi-cursor-pointer</v-icon>
                </v-btn>
              </v-expansion-panel-content>
            </v-expansion-panel>
          </v-expansion-panels>
        </tbody>
      </template>
    </v-simple-table>
    
  </div>
</template>

<script>
import axios from 'axios';
export default {
  data() {
    return {
      searchInfo: null,     // 입력된 음식명
      searchResults : [],   // 검색된 음식리스트
      carbPercent: 0,
      protPercent: 0,
      fatPercent: 0,
      openCarb: 0,
      openProt: 0,
      openFat: 0,
      openTotal: 0,
      more: false,
      subFoodList : [],
      updateNeeded: true,
      searchValid: false,
    }
  },
  watch:{
    searchResults : function(){
      this.more = false;
      this.subFoodList = null;
      this.updateNeeded = true;
      this.searchValid = true;
    },
  },
  methods: {
    foodSearch() {
      axios.get(`/apis/namefood/${this.searchInfo}`)
      .then((response) => {
        this.searchResults = response.data;
      })
    },
    dietPick(data) {
      this.$store.dispatch('PickFood', data);
      this.$router.push(`/writefood/form`)
    },
    setPercentBar(carb, prot, fat, target){
      // var $carb = document.getElementsByClassName('carb')[0]
      // var $prot = document.getElementsByClassName('prot')[0]
      // var $fat  = document.getElementsByClassName('fat')[0]

      // console.dir(target)

     let el = target.nextElementSibling
    //  console.dir(el)
     if(el){
       // $carb.html(carb + '%').css({ width : carb + '%'});
        // $prot.html(prot + '%').css({ width : prot + '%'});
        // $fat.html(fat + '%').css({ width : fat + '%'});
        // console.log(typeof(el))
        var $carb = el.getElementsByClassName('carb')[0]
        var $prot = el.getElementsByClassName('prot')[0]
        var $fat = el.getElementsByClassName('fat')[0]
        
        if($carb && $prot && $fat){
          $carb.style.width = carb + '%';
          $carb.innerHTML = carb + '%';
  
          $prot.style.width = prot + '%';
          $prot.innerHTML = prot + '%';
  
          $fat.style.width = fat + '%'
          $fat.innerHTML = fat + '%'; 
        }
        
      }else{
        setTimeout(() => {
          this.setPercentBar(carb, prot, fat, target)
        }, 200)
      }
    },
    openFood(data) {
      let target = event.target;

      this.openCarb = 0
      this.openProt = 0
      this.openFat = 0
      this.openTotal = 0
      this.carbPercent = 0
      this.protPercent = 0
      this.fatPercent = 0
      
      for (var n of data) {
        if (n.nutrient == 'pro') {
          this.openProt = n.content * 4
        } else if (n.nutrient == 'carb') {
          this.openCarb = n.content * 4
        } else if (n.nutrient == 'fat') {
          this.openFat = n.content * 9
        }
      }
      this.openTotal = this.openCarb + this.openProt + this.openFat
      
      if (this.openTotal) {
        this.carbPercent = (this.openCarb / this.openTotal) * 100
        this.protPercent = (this.openProt / this.openTotal) * 100
        this.fatPercent = (this.openFat / this.openTotal) * 100
      }

      var carb = Math.floor(this.carbPercent);
      var prot = Math.floor(this.protPercent);
      var fat = Math.floor(this.fatPercent);
      
      this.setPercentBar(carb, prot, fat, target)
    },
    showMore(){
      this.more = !this.more
      if(this.more && this.updateNeeded){
        axios.get(`/apis/searchsubfood/${this.searchInfo}`)
        .then( ({data}) => {
          this.subFoodList = data;
        })
        .catch( () => {
          alert('error')  
        })
      }

      this.updateNeeded = false;
    }
  }
}
</script>

<style scoped>
.v-expansion-panel::before {
  box-shadow: none;
}
.percent-bar {
  width: 100%;
  height: 20px;
  border-radius: 4px;
  overflow: hidden;
}

.percent-bar--part {
  float: left;
  overflow: hidden;
  color: #fff;
  text-align: center;
  font-family: Arial;
  font-size: .9em;
  height: 100%;
  padding-top: 2px;
}

.carb {
  background-color: #BF4C3D;
  width: 0%;
  border-right: 1px solid #fff;
  box-sizing: border-box;
}

.prot {
  background-color: green;
  width: 0%;
  border-right: 1px solid #fff;
}
.fat {
  background-color: #1A7ABA;
  width: 0%;
}
</style>