var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _vm._m(0)}
var staticRenderFns = [function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',[_vm._v("\n  제 1 장 총칙"),_c('br'),_c('br'),_vm._v("\n  제1조 (목적)"),_c('br'),_c('br'),_vm._v("\n  1.본 약관은 SSAZIT(이하 '센터'라고 함)이 제공하는 각종 정보서비스"),_c('br'),_vm._v("\n  (이하 '서비스'라고 함)의 이용 조건 및 절차에 관한 사항과 기타 필요한 사항을 규정함을 목적으로 합니다."),_c('br'),_c('br'),_vm._v("\n  제2조 (정의)"),_c('br'),_vm._v("\n  1.[이용자]란 센터 홈페이지에 접속하여 센터가 제공하는 서비스를 받는 자를 말합니다."),_c('br'),_vm._v("\n  2.[회원]이란 센터에 개인정보를 제공하여 센터와 서비스 이용 계약을 체결한 자로 센터 홈페이지를 통해 과제 응모를 할 수 있으며, 과제 심사 진행과 관련된 서비스를 받을 수 있습니다."),_c('br'),_vm._v("\n  3.[개인정보]란 당해 정보에 포함되어 있는 성명 등의 사항에 의하여 특정개인을 식별할 수 있는 정보(당해 정보만으로는 특정개인을 인식할 수 없더라도 다른 정보와 용이하게 결합하여 식별할 수 있는 것을 포함한다)를 말합니다."),_c('br'),_vm._v("\n  4.[회원 아이디] (이하 'ID' 라 합니다)란 회원의 식별과 서비스 이용을 위하여 회원이 입력한 회원의 이메일 주소를 의미합니다."),_c('br'),_vm._v("\n  5.[비밀번호]란 회원이 부여받은 ID와 일치된 회원임을 확인하고, 회원의 개인정보를 보호하기 위하여 회원이 정한 문자와 숫자의 조합을 의미합니다."),_c('br'),_vm._v("\n  6.[등록해지] (이하 '해지' 라 함)란 센터 또는 회원이 이용계약을 해지하는 것을 의미합니다."),_c('br'),_c('br'),_vm._v("\n  제3조 (약관 등의 명시와 설명 및 개정)"),_c('br'),_c('br'),_vm._v("\n  1.센터는 본 약관의 내용을 회원이 쉽게 알 수 있도록 각 서비스 사이트의 초기 서비스화면에 게시합니다. 다만, 약관의 내용은 이용자가 연결화면을 통하여 볼 수 있도록 할 수 있습니다. "),_c('br'),_vm._v("\n  2.센터는 약관의규제에관한법률, 전자문서 및 전자거래기본법, 전자서명법, 정보통신망이용촉진및정보보호등에관한법률 등 관련법을 위배하지 않는 범위에서 본 약관을 개정할 수 있습니다."),_c('br'),_vm._v("\n  3.센터가 본 약관을 개정할 경우에는 적용일자 및 개정사유를 명시하여 현행 약관과 함께 센터가 제공하는 서비스 사이트의 초기 화면에 그 적용일자 7일 이전부터 적용일자 전일까지 공지합니다. 다만, 회원에게 불리하게 약관내용을 변경하는 경우에는 최소한 30일 이상의 사전 유예기간을 두고 공지합니다. 이 경우 센터는 개정 전 내용과 개정 후 내용을 명확하게 비교하여 회원이 알기 쉽도록 표시합니다. "),_c('br'),_vm._v("\n  4.회원은 개정된 약관에 대해 거부할 권리가 있습니다. 회원은 개정된 약관에 동의하지 않을 경우 서비스 이용을 중단하고 회원등록을 해지할 수 있습니다. 단, 개정된 약관의 효력 발생일 이후에도 서비스를 계속 이용할 경우에는 약관의 변경사항에 동의한 것으로 간주합니다. "),_c('br'),_vm._v("\n  5.변경된 약관에 대한 정보를 알지 못해 발생하는 회원 피해는 센터가 책임지지 않습니다."),_c('br'),_c('br'),_vm._v("\n  제4조 (약관 외 준칙) "),_c('br'),_c('br'),_vm._v("\n  1.본 약관은 센터가 제공하는 서비스에 관해 별도의 정책 및 운영규칙과 함께 적용됩니다. "),_c('br'),_vm._v("\n  2.본 약관에 명시되지 아니한 사항과 본 약관의 해석에 관하여는 관계법령에 따릅니다."),_c('br'),_c('br'),_c('br'),_vm._v("\n  제 2 장 서비스 이용 계약"),_c('br'),_c('br'),_vm._v("\n  제5조 (이용 계약의 성립) "),_c('br'),_c('br'),_vm._v("\n  1.이용계약은 회원의 본 이용약관 내용에 대한 동의와 이용신청에 대하여 센터의 이용 승낙으로 성립합니다. "),_c('br'),_vm._v("\n  2.본 이용 약관에 대한 동의는 회원등록 당시 본 약관을 읽고 \"위 서비스 약관에 동의합니다\"의 대화창에 표시를 한 후 등록하기 단추를 누름으로써 의사표시를 한 것으로 간주합니다."),_c('br'),_c('br'),_c('br'),_vm._v("\n  제6조 (서비스 이용 신청) "),_c('br'),_c('br'),_vm._v("\n  1.회원으로 가입하여 본 서비스를 이용하고자 하는 이용고객은 센터에서 요청하는 제반정보(이름, 메일주소, 연락처, 주소 등)를 제공하여야 합니다.  "),_c('br'),_vm._v("\n  2.타인의 명의(이름 및 메일주소 등)를 도용하여 이용신청을 한 회원의 모든 ID는 삭제되며, 관계법령에 따라 처벌을 받을 수 있습니다."),_c('br'),_vm._v("\n  3.만14세 미만의 아동이 회원으로 가입하기 위해서는 반드시 개인정보의 수집 및 이용목적에 대하여 충분히 숙지하고 법정대리인(부모)의 동의를 받아야 합니다. 법정대리인의 허락을 받지 않은 14세 미만의 아동에 대해서는 회원에서 제외할 수 있습니다. "),_c('br'),_c('br'),_c('br'),_vm._v("\n  제 7 조 (개인정보의 보호 및 사용) "),_c('br'),_c('br'),_vm._v("\n  1.센터는 관계법령이 정하는 바에 따라 회원등록정보를 포함한 회원의 개인정보를 보호하기 위해 노력 합니다. 회원 개인 정보의 보호 및 사용에 대해서는 관련법령 및 센터의 개인정보보호정책이 적용됩니다. 단, 센터의 공식사이트 이외의 웹에서 링크된 사이트에서는 센터의 개인정보보호정책이 적용되지 않습니다. 또한 센터는 회원의 귀책사유로 인해 노출된 정보에 대해서 일체의 책임을 지지 않습니다. "),_c('br'),_vm._v("\n  2.센터는 이용자에게 제공하는 서비스의 양적, 질적 향상을 위하여 이용자의 개인정보를 제휴사에게 제공, 공유할 수 있으며, 이 때에는 반드시 이용자의 동의를 받아 필요한 최소한의 정보를 제공, 공유하며 누구에게 어떤 목적으로 어떤 정보를 제공, 공유하는지 명시합니다."),_c('br'),_vm._v("\n  3.회원은 원하는 경우 언제든 센터에 제공한 개인정보의 수집과 이용에 대한 동의를 철회할 수 있으며, 동의의 철회는 회원 탈퇴를 하는 것으로 이루어집니다."),_c('br'),_c('br'),_c('br'),_vm._v("\n  제8조 (이용 신청의 승낙과 제한) "),_c('br'),_c('br'),_vm._v("\n  1.센터는 제 6조의 규정에 의한 이용 신청고객에 대하여 업무 수행상 또는 기술상 지장이 없는 경우에 원칙적으로 접수 순서에 따라 서비스 이용을 승낙합니다. "),_c('br'),_vm._v("\n  2.센터는 다음 각 호의 내용에 해당하는 경우 이용신청에 대한 승낙을 제한할 수 있고, 그 사유가 해소 될 때까지 승낙을 유보할 수 있습니다. "),_c('br'),_vm._v("\n  ① 센터의 서비스 관련 설비에 여유가 없는 경우 "),_c('br'),_vm._v("\n  ② 센터의 기술상 선정에 지장이 있는 경우"),_c('br'),_vm._v("\n  ③ 기타 센터의 사정상 필요하다고 인정되는 경우"),_c('br'),_vm._v("\n  3.센터는 다음 각 호의 내용에 해당하는 경우 이용신청에 대한 승낙을 하지 아니할 수도 있습니다. "),_c('br'),_vm._v("\n  ① 실명이 아니거나 타인의 명의를 이용하여 신청한 경우 "),_c('br'),_vm._v("\n  ② 이용계약 신청서의 내용을 허위로 기재한 경우 "),_c('br'),_vm._v("\n  ③ 사회의 안녕과 질서, 미풍양속을 저해할 목적으로 신청한 경우"),_c('br'),_vm._v("\n  ④ 부정한 용도로 본 서비스를 이용하고자 하는 경우"),_c('br'),_vm._v("\n  ⑤ 영리를 추구할 목적으로 본 서비스를 이용하고자 하는 경우 "),_c('br'),_vm._v("\n  ⑥ 기타 센터가 정한 등록신청 요건이 미비된 경우 "),_c('br'),_vm._v("\n  ⑦ 본 서비스와 경쟁관계가 있는 이용자가 신청하는 경우 "),_c('br'),_vm._v("\n  ⑧ 기타 규정한 제반사항을 위반하며 신청하는 경우"),_c('br'),_vm._v("\n  4.센터는 이용신청고객이 관계법령에서 규정하는 미성년자일 경우에 서비스별 안내에서 정하는 바에 따라 승낙을 보류할 수 있습니다. "),_c('br'),_c('br'),_c('br'),_vm._v("\n  제9조 (회원 아이디 부여 및 변경 등) "),_c('br'),_c('br'),_vm._v("\n  1.센터는 회원에 대하여 본 약관에 정하는 바에 따라 회원 ID를 부여합니다. "),_c('br'),_vm._v("\n  2.회원이 원할 경우 회원 ID로 사용되는 메일주소는 변경이 가능하며, 이 때 새로운 회원 ID로 사용될 메일주소는 다른 회원이 ID로 사용하는 메일주소와 중복되지 않아야 합니다."),_c('br'),_vm._v("\n  3.회원은 발행물 구독 관리 또는 기타 해당 페이지로 링크된 메뉴를 통하여 자신의 개인정보를 관리할 수 있는 페이지를 열람할 수 있으며, 해당 페이지에서 언제든지 본인의 개인정보를 열람하고 수정할 수 있습니다. "),_c('br'),_vm._v("\n  4.회원 ID는 다음 각 호에 해당하는 경우에 회원 또는 센터의 요청으로 변경할 수 있습니다. "),_c('br'),_vm._v("\n  ① 회원 ID가 회원의 전화번호 또는 주민등록번호 등으로 등록되어 사생활 침해가 우려되는 경우"),_c('br'),_vm._v("\n  ② 타인에게 혐오감을 주거나 미풍양속에 어긋나는 경우 "),_c('br'),_vm._v("\n  ③ 기타 합리적인 사유가 있는 경우"),_c('br'),_vm._v("\n  5.회원 ID 및 비밀번호의 관리책임은 회원에게 있습니다. 이를 소홀이 관리하여 발생하는 서비스 이용상의 손해 또는 제3자에 의한 부정이용 등에 대한 책임은 회원에게 있으며 센터는 그에 대한 책임을 일절 지지 않습니다. "),_c('br'),_vm._v("\n  6.기타 회원개인정보 관리 및 변경에 관한 사항은 서비스별 안내에 정하는 바에 의합니다."),_c('br'),_c('br'),_c('br'),_vm._v("\n  제10조 (센터의 의무) "),_c('br'),_c('br'),_vm._v("\n  1.센터는 관련법과 본 약관이 금지하거나 공서양속에 반하는 행위를 하지 않으며, 본 약관이 정하는 바에 따라 지속적이고, 안정적으로 서비스를 제공하기 위하여 최선을 다하여야 합니다."),_c('br'),_vm._v("\n  2.센터는 회원이 안전하게 서비스를 이용할 수 있도록 회원의 개인정보보호를 위한 보안시스템을 구축하며 개인정보 보호정책을 공시하고 준수합니다. "),_c('br'),_vm._v("\n  3.센터는 수신거절의 의사를 명백히 표시한 회원에 대해서는 회원이 원하지 않는 영리목적의 광고성 전자우편(이메일)을 발송하지 않습니다. "),_c('br'),_vm._v("\n  4.센터는 이용계약의 체결, 계약사항의 변경 및 해지 등 회원과의 계약관계절차 및 내용 등에 있어 회원에게 편의를 제공하도록 노력합니다. 센터 관리자에게 회원이 탈퇴 신청 메일을 보낼 경우 회원 본인임을 확인한 후 탈퇴 처리를 합니다."),_c('br'),_vm._v("\n  5.센터는 이용고객으로부터 제기되는 의견이나 불만이 정당하다고 객관적으로 인정될 경우에는 적절한 절차를 거쳐 즉시 처리하여야 합니다. 다만, 즉시 처리가 곤란한 경우는 이용자에게 그 사유와 처리 일정을 통보하여야 합니다. "),_c('br'),_c('br'),_c('br'),_vm._v("\n  제11조 (회원 ID 및 비밀번호에 대한 의무) "),_c('br'),_c('br'),_vm._v("\n  1.센터는 관계법령 및 개인정보보호정책에 의하여 그 책임을 지는 경우를 제외하고, 회원 ID와 비밀번호의 관리책임은 회원에게 있습니다."),_c('br'),_vm._v("\n  2.회원은 자신의 ID 및 비밀번호를 제3자가 이용하게 해서는 안됩니다."),_c('br'),_vm._v("\n  3.회원이 자신의 ID 및 비밀번호를 도용 당하거나 제3자가 사용하고 있음을 인지한 경우에는 바로 센터에 통보하고 센터의 안내가 있는 경우에는 그에 따라야 합니다."),_c('br'),_vm._v("\n  4.센터는 회원이 상기 제1항, 제2항, 제3항을 위반하여 회원에게 발생한 손해에 대하여 책임을 일절 지지 않습니다."),_c('br'),_c('br'),_c('br'),_vm._v("  \n  제12조 (회원의 의무) "),_c('br'),_c('br'),_vm._v("\n  1.회원은 본 약관에 규정하는 사항과 기타 센터가 정한 제반 규정, 서비스 이용안내 또는 공지사항 등 센터가 공지 또는 통지하는 사항 및 관계법령을 준수하여야 하며, 기타 센터의 업무에 방해가 되는 행위, 센터의 명예를 손상 시키는 행위를 하여서는 안됩니다."),_c('br'),_vm._v("\n  2.센터가 관계법령 및 개인정보보호정책에 의하여 그 책임을 지는 경우를 제외하고 제11조의 관리소홀, 부정사용에 의하여 발생되는 모든 결과에 대한 책임은 회원에게 있습니다."),_c('br'),_vm._v("\n  3.회원은 센터의 사전승낙 없이 서비스를 이용하여 영업활동을 할 수 없으며, 회원 간 또는 회원과 제3자 간에 서비스를 매개로 한 물품거래 및 서비스의 이용과 관련하여 기대하는 이익 등에 관하여 발생한 손해에 대하여 센터는 책임을 지지 않습니다. 이와 같은 영업활동으로 센터가 손해를 입은 경우 회원은 센터에 대하여 손해배상의 의무를 지며, 센터는 해당 회원에 대해 서비스 이용제한 및 적법한 절차를 거쳐 손해배상 등을 청구할 수 있습니다. "),_c('br'),_vm._v("\n  4.회원은 센터의 명시적인 동의가 없는 한 서비스의 이용권한, 기타 이용계약 상의 지위를 타인에게 양도, 증여할 수 없으며, 이를 담보로 제공할 수 없습니다."),_c('br'),_vm._v(" \n  5.회원은 서비스 이용과 관련하여 제22조 제1항의 각 호에 해당하는 행위를 하여서는 안됩니다. "),_c('br'),_vm._v("\n  6.이용자는 회원가입 신청 또는 회원정보 변경 시 모든 사항을 사실에 근거하여 작성하여야 하며, 허위 또는 타인의 정보를 등록할 경우 일체의 권리를 주장할 수 없습니다."),_c('br'),_vm._v("\n  7.회원은 주소, 연락처, 전자우편 주소 등 이용계약사항이 변경된 경우에 해당 절차를 거쳐 이를 센터에 즉시 알려야 합니다."),_c('br'),_vm._v("\n  8.회원은 센터 및 제3자의 지적 재산권을 침해해서는 안됩니다. "),_c('br'),_vm._v("\n  9.회원은 다음 각 호에 해당하는 행위를 하여서는 안되며, 해당 행위를 하는 경우에 센터는 회원의 서비스 이용제한 및 적법 조치를 포함한 제재를 가할 수 있습니다."),_c('br'),_vm._v("\n  ① 회원가입 신청 또는 회원정보 변경 시 허위내용을 등록하는 행위"),_c('br'),_vm._v("\n  ② 다른 이용자의 ID, 비밀번호, 주민등록번호를 도용하는 행위"),_c('br'),_vm._v("\n  ③ 이용자 ID를 타인과 거래하는 행위"),_c('br'),_vm._v("\n  ④ 센터의 운영진, 직원 또는 관계자를 사칭하는 행위"),_c('br'),_vm._v("\n  ⑤ 센터으로부터 특별한 권리를 부여받지 않고 센터의 클라이언트 프로그램을 변경하거나, 센터의 서버를 해킹하거나, 웹사이트 또는 게시된 정보의 일부분 또는 전체를 임의로 변경하는 행위"),_c('br'),_vm._v("\n  ⑥ 서비스에 위해를 가하거나 고의로 방해하는 행위 "),_c('br'),_vm._v("\n  ⑦ 본 서비스를 통해 얻은 정보를 센터의 사전 승낙 없이 서비스 이용 외의 목적으로 복제하거나, 이를 출판 및 방송 등에 사용하거나, 제 3자에게 제공하는 행위"),_c('br'),_vm._v("\n  ⑧ 센터 또는 제3자의 저작권 등 기타 지적재산권을 침해하는 내용을 전송, 게시, 전자우편 또는 기타의 방법으로 타인에게 유포하는 경우"),_c('br'),_vm._v("\n  ⑨ 공공질서 및 미풍양속에 위반되는 저속, 음란한 내용의 정보, 문장, 도형, 음향, 동영상을 전송, 게시, 전자우편 또는 기타의 방법으로 타인에게 유포하는 행위"),_c('br'),_vm._v("\n  ⑩ 모욕적이거나 개인신상에 대한 내용이어서 타인의 명예나 프라이버시를 침해할 수 있는 내용을 전송, 게시, 전자우편 또는 기타의 방법으로 타인에게 유포하는 행위 "),_c('br'),_vm._v("\n  ⑪ 다른 이용자를 희롱 또는 위협하거나, 특정 이용자에게 지속적으로 고통 또는 불편을 주는 행위"),_c('br'),_vm._v("\n  ⑫ 센터의 승인을 받지 않고 다른 사용자의 개인정보를 수집 또는 저장하는 행위"),_c('br'),_vm._v("\n  ⑬ 범죄와 결부된다고 객관적으로 판단되는 행위"),_c('br'),_vm._v("\n  ⑭ 본 약관을 포함하여 기타 센터가 정한 제반 규정 또는 이용 조건을 위반하는 행위"),_c('br'),_vm._v("\n  ⑮ 기타 관계법령에 위배되는 행위 "),_c('br'),_c('br'),_c('br'),_vm._v("\n  제 3 장 서비스의 이용"),_c('br'),_c('br'),_vm._v("\n  제13조 (서비스의 제공 및 변경) "),_c('br'),_vm._v("\n  1.센터는 센터가 제공하는 서비스에서 진행하는 컨텐츠와 이벤트 등의 모든 서비스를 회원에게 제공합니다."),_c('br'),_vm._v("\n  ① 사업정보 제공 서비스"),_c('br'),_vm._v("\n  ② 사업정보 통합검색 서비스"),_c('br'),_vm._v("\n  ③ 고객지원(Q&A) 서비스"),_c('br'),_vm._v("\n  ④ 기타 센터가 정하는 서비스"),_c('br'),_vm._v("\n  2.센터에서 제공하는 서비스는 기본적으로 무료입니다. 유료서비스 추가 시 사전 공지하며 이용에 대한 사항은 센터가 별도로 정한 서비스 약관 및 정책 또는 운영규칙에 따릅니다. "),_c('br'),_vm._v("\n  3.센터는 서비스 변경 시 그 변경될 서비스의 내용 및 제공일자를 제 14조에 정한 방법으로 회원에게 통지합니다."),_c('br'),_c('br'),_vm._v("\n  제14조 (정보의 제공 및 통지) "),_c('br'),_vm._v("\n  1.센터는 회원이 서비스 이용 중 필요하다고 인정되는 다양한 정보를 공지사항 혹은 전자우편 등의 방법으로 회원에게 제공할 수 있습니다. "),_c('br'),_vm._v("\n  2.센터는 불특정다수 회원에 대한 통지를 하는 경우 7일 이상 공지 게시판에 게시함으로써 개별 통지에 갈음할 수 있습니다."),_c('br'),_c('br'),_c('br'),_vm._v("\n  제15조 (게시물의 저작권 및 관리) "),_c('br'),_vm._v("\n  '정보통신윤리위원회규칙'을 기본 원칙으로 하며 다음과 같은 내용의 게시물은 등록을 금합니다. "),_c('br'),_vm._v("\n  1.센터는 회원의 게시물을 소중하게 생각하며 변조, 훼손, 삭제되지 않도록 최선을 다하여 보호합니다. 다만, 다음 각 호에 해당하는 게시물이나 자료의 경우 사전통지 없이 삭제하거나 이동 또는 등록 거부를 할 수 있으며, 해당 회원의 자격을 제한, 정지 또는 상실시킬 수 있습니다. . "),_c('br'),_vm._v("\n  ① 다른 회원 또는 제3자에게 심한 모욕을 주거나 명예를 손상시키는 내용인 경우"),_c('br'),_vm._v("\n  ② 공공질서 및 미풍양속에 위반되는 내용을 유포하거나 링크시키는 경우"),_c('br'),_vm._v("\n  ③ 불법복제 또는 해킹을 조장하는 내용인 경우"),_c('br'),_vm._v("\n  ④ 영리를 목적으로 하는 광고일 경우 "),_c('br'),_vm._v("\n  ⑤ 범죄적 행위에 결부된다고 인정되는 내용인 경우"),_c('br'),_vm._v("\n  ⑥ 센터가 다른 회원의 저작권 혹은 제3자의 저작권 등 기타 권리를 침해하는 내용인 경우 "),_c('br'),_vm._v("\n  ⑦ 센터에서 규정한 게시물 원칙에 어긋나거나, 게시판 성격에 부합하지 않는 경우"),_c('br'),_vm._v("\n  ⑧ 스팸성 게시물인 경우"),_c('br'),_vm._v("\n  ⑨ \"센터\"의 정상적인 경영활동 및 서비스를 방해하고 센터 이익에 막대한 지장과 해악을 미친다고 판단되는 게시물"),_c('br'),_vm._v("\n  ⑩ 기타 관계법령에 위배된다고 판단되는 경우"),_c('br'),_vm._v("\n  ⑪\"이용자\"들에게 피해 및 손해를 입힐 우려가 있다고 판단되는 게시물"),_c('br'),_vm._v("\n  2.센터가 작성한 저작물에 대한 저작권 및 기타 지적재산권은 센터에 귀속됩니다. "),_c('br'),_vm._v("\n  3.회원이 서비스 화면 내에 게시한 게시물의 저작권은 게시한 회원에게 귀속됩니다. 또한 센터는 게시자의 동의없이 게시물을 상업적으로 이용할 수 없습니다. 다만, 비영리 목적인 경우는 그러하지 아니하며, 또한 본 사이트 내에서의 게재권을 갖습니다. "),_c('br'),_vm._v("\n  4.회원은 서비스를 이용하여 취득한 정보를 센터의 사전 승낙없이 임의가공, 판매, 복제, 송신, 출판, 배포, 방송 기타 방법에 의하여 영리목적으로 이용하거나 제3자에게 이용하게 하여서는 안됩니다. "),_c('br'),_c('br'),_c('br'),_vm._v("\n  제16조 (광고게재 및 광고주와의 거래) "),_c('br'),_vm._v("\n  1.센터가 제공하는 서비스 내에 다양한 배너와 링크(Link)를 포함할 수 있으며, 이는 광고주와의 계약관계에 의하거나 제공받은 컨텐츠의 출처를 밝히기 위한 조치입니다. 회원은 서비스 이용 시 노출되는 광고게재에 대해 동의합니다."),_c('br'),_vm._v("\n  2.서비스 내에 포함되어 있는 링크를 클릭하여 타 사이트의 페이지로 옮겨 갈 경우 해당 사이트의 개인정보보호정책은 센터는 무관합니다."),_c('br'),_c('br'),_c('br'),_vm._v("\n  제17조 (서비스 이용시간) "),_c('br'),_vm._v("\n  1.서비스 이용은 센터의 업무상 또는 기술상 특별한 지장이 없는 한 연중 무휴, 1일 24시간 운영을 원칙으로 합니다. 다만, 센터는 시스템 정기 점검, 증설 및 교체를 위해 센터가 정한 날이나 시간에 서비스를 일시 중단할 수 있으며, 예정되어 있는 작업으로 인한 서비스 일시중단은 센터가 제공하는 서비스를 통해 사전에 공지합니다."),_c('br'),_vm._v(" \n  2.센터는 서비스를 일정 범위로 분할하여 각 범위별로 이용 가능시기를 별도로 지정할 수 있습니다. 다음에 해당하는 서비스의 경우, 이용 가능 시기를 과제접수 시작일로부터 협약까지로 제한합니다. "),_c('br'),_vm._v("\n  ① 과제 접수 및 심사 진행 관련 정보"),_c('br'),_c('br'),_c('br'),_vm._v("\n  제18조 (서비스 제공의 중단 등) "),_c('br'),_vm._v("\n  1.센터는 다음 각 호의 내용에 해당하는 경우 서비스 제공의 일부 혹은 전부를 제한하거나 중단할 수 있습니다."),_c('br'),_vm._v("\n  ① 정보통신설비의 보수 점검, 교체 및 고장 등 공사로 인한 부득이한 경우 "),_c('br'),_vm._v("\n  ② 기간통신사업자가 전기통신서비스를 중단했을 경우"),_c('br'),_vm._v("\n  ③ 서비스 이용의 폭주 등으로 정상적인 서비스 이용에 지장이 있는 경우 "),_c('br'),_vm._v("\n  ④ 국가비상사태 등 기타 불가 항력적인 사유가 있는 경우"),_c('br'),_vm._v("\n  2.전항에 의한 서비스 중단의 경우에는 센터는 제14조에 정한 방법으로 그 사유 및 기간 등을 공지합니다. 다만, 센터가 통제할 수 없는 사유로 발생한 서비스의 중단 (시스템관리자의 고의, 과실없는 디스크 장애, 시스템 다운 등)에 대하여 사전공지가 불가능한 경우에는 예외로 합니다."),_c('br'),_c('br'),_c('br'),_vm._v("\n  제19조 (회원 ID 관리) "),_c('br'),_vm._v("\n  1.회원 ID와 비밀번호에 관한 모든 관리책임은 회원에게 있습니다. "),_c('br'),_vm._v("\n  2.센터는 회원 ID에 의하여 제반 회원 관리업무를 수행하므로 회원이 ID를 변경하고자 하는 경우 센터가 인정할 만한 사유가 없는 한 ID의 변경을 제한할 수 있습니다. "),_c('br'),_vm._v("\n  3.회원이 등록한 회원 ID 및 비밀번호에 의하여 발생되는 사용상의 과실 또는 제3자에 의한 부정사용 등에 대한 모든 책임은 해당 이용고객에게 있습니다. "),_c('br'),_c('br'),_c('br'),_vm._v("\n  제20조 (정보의 제공) "),_c('br'),_vm._v("\n  1.센터는 회원에게 서비스 이용에 필요가 있다고 인정되는 각종 정보에 대해서 전자우편이나 서신우편 등의 방법으로 회원에게 제공할 수 있습니다. "),_c('br'),_vm._v("\n  2.센터는 서비스 개선 및 회원 대상의 서비스 소개 등의 목적으로 회원의 동의 하에 추가적인 개인 정보를 요구할 수 있습니다."),_c('br'),_c('br'),_c('br'),_vm._v("\n  제21조 (계약 변경 및 해지) "),_c('br'),_vm._v("\n  1.회원이 이용계약을 해지하고자 하는 경우에는 회원 본인이 사이트 내의 개인정보관리 페이지를 통해 등록 해지신청을 하여야 합니다. "),_c('br'),_c('br'),_c('br'),_vm._v("\n  제 22조 (서비스 이용 제한) "),_c('br'),_vm._v("\n  1.회원은 본 약관 제11조, 제12조 내용을 위반하거나 다음 각 호에 해당하는 행위를 하는 경우에 센터는 회원의 서비스 이용을 제한할 수 있습니다."),_c('br'),_vm._v("\n  ① 미풍양속을 저해하는 비속한 ID 및 별명 사용 "),_c('br'),_vm._v("\n  ② 타 이용자에게 심한 모욕을 주거나, 서비스 이용을 방해한 경우"),_c('br'),_vm._v("\n  ③ 정보통신 윤리위원회 등 관련 공공기관의 시정 요구가 있는 경우"),_c('br'),_vm._v("\n  ④ 1년 이상 서비스를 이용한 적이 없는 경우"),_c('br'),_vm._v("\n  ⑤ 불법 게시물을 게재한 경우"),_c('br'),_vm._v("\n  ⑥ 상용소프트웨어나 크랙파일을 올린 경우"),_c('br'),_vm._v("\n  ⑦ 정보통신윤리위원회의 심의 세칙 제 7조에 어긋나는 음란물을 게재한 경우"),_c('br'),_vm._v("\n  ⑧ 반국가적 행위의 수행을 목적으로 하는 내용을 포함한 경우 "),_c('br'),_vm._v("\n  ⑨ 저작권이 있는 글을 무단 복제하거나 파일을 게재한 경우"),_c('br'),_vm._v("\n  ⑩ 기타 정상적인 서비스 운영에 방해가 될 경우"),_c('br'),_vm._v("\n  2.상기 이용제한 규정에 따라 서비스를 이용하는 회원에게 서비스 이용에 대하여 별도 공지 없이 서비스 이용의 일시정지, 초기화, 이용계약 해지 등을 불량이용자 처리규정에 따라 취할 수 있으며, 회원은 전항의 귀책사유로 인하여 센터가 다른 회원에게 입힌 손해를 배상할 책임이 있습니다. "),_c('br'),_c('br'),_c('br'),_vm._v("\n  제 4 장 손해배상 및 재판관할"),_c('br'),_c('br'),_vm._v("\n  제 23 조 (손해 배상) "),_c('br'),_vm._v("\n  1.센터는 무료로 제공하는 서비스의 이용과 관련하여 개인정보보호정책에서 정하는 내용에 해당하지 않는 사항에 대하여는 어떠한 손해도 책임을 지지 않습니다."),_c('br'),_c('br'),_c('br'),_c('br'),_vm._v("\n  제 24 조 (면책 조항) "),_c('br'),_vm._v("\n  1.센터는 천재지변, 전쟁 및 기타 불가항력, 센터의 합리적인 통제범위를 벗어난 사유로 인하여 서비스를 제공할 수 없는 경우에는 그에 대한 책임이 면제됩니다. "),_c('br'),_vm._v("\n  2.센터는 기간통신 사업자가 전기통신 서비스를 중지하거나 정상적으로 제공하지 아니하여 손해가 발생한 경우 책임이 면제됩니다."),_c('br'),_vm._v("\n  3.센터는 서비스용 설비의 보수, 교체, 정기점검, 공사 등 부득이한 사유로 발생한 손해에 대한 책임이 면제됩니다. "),_c('br'),_vm._v("\n  4.센터는 이용자의 귀책사유로 인한 서비스 이용의 장애 또는 손해에 대하여 책임을 지지 않습니다."),_c('br'),_vm._v("\n  5.센터는 이용자의 컴퓨터 오류에 의해 손해가 발생한 경우, 또는 회원이 신상정보 및 전자우편 주소를 부실하게 기재하여 손해가 발생한 경우 책임을 지지 않습니다."),_c('br'),_vm._v("\n  6.센터는 회원이 서비스를 이용하여 기대하는 수익을 얻지 못하거나 상실한 것에 대하여 책임을 지지 않습니다. "),_c('br'),_c('br'),_vm._v("\n  제 25 조 (분쟁의 해결)"),_c('br'),_vm._v("\n  1.센터와 회원은 서비스와 관련하여 발생한 분쟁을 원만하게 해결하기 위하여 필요한 노력을 합니다."),_c('br'),_vm._v("\n  2.센터는 회원으로부터 제출되는 불만사항 및 의견을 우선적으로 처리합니다. 다만, 신속한 처리가 곤란한 경우에는 회원에게 그 사유와 처리일정을 즉시 통보합니다. "),_c('br'),_c('br'),_c('br'),_vm._v("\n  제 26 조 (재판권 및 준거법) "),_c('br'),_vm._v("\n  1.센터와 회원 간에 서비스 이용으로 발생한 분쟁에 대하여는 대한민국법을 적용하며, 본 분쟁으로 인하여 소송이 제기될 경우 민사소송법 상의 관할을 가지는 대한민국의 법원에 제기합니다. "),_c('br'),_c('br'),_c('br'),_vm._v("\n  - 부칙"),_c('br'),_vm._v("\n  본 약관은 2013년 8월 19일부터 시행합니다.\n")])}]

